.home {
	.color-block {
		position: fixed;
		height: 200%;
		width: 100%;
		transform: rotate(-15deg);
		left: -83%;
		top: -50%;
    z-index: -1 !important;
	}
	.bg {
		background-image: url(../image/2.jpg);
		background-size: cover;
		background-repeat: no-repeat;
		background-position: top;
		height: calc(100vh - 80px);
		z-index: 111;
		border-radius: 30px;
		left: 40px;
		top: 40px;
		box-shadow: 0 0 7px rgba(0,0,0,.9);
    position: absolute;
	}
	.container-home {
		display: flex;
		align-items: center;
		min-height: 100%;
	}
	.home-details {
		>div {
			max-width: 550px;
			margin: 0 auto;
		}
		h1 {
			font-size: 51px;
			line-height: 62px;
			font-weight: 700;
			margin: 18px 0 10px;
			position: relative;
			padding-left: 70px;
			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: 29px;
				height: 4px;
				width: 40px;
				border-radius: 10px;
			}
			span {
				color: #fff;
				display: block;
			}
		}
		h6 {
			color: #eee;
			font-size: 22px;
			font-weight: 400;
			display: block;
		}
		p {
			margin: 15px 0 28px;
			font-size: 16px;
			line-height: 35px;
		}
	}
}

@media (min-width: 992px) {
	.home {
		.home-details-container {
			width: 100%;
			height: 100vh;
		}
	}
}

@media (max-width: 991px) {
	body.light {
		.main-img-mobile {
			border: 4px solid #eee;
		}
	}
	body.home {
		padding: 25px;
		.main-container {
			padding: 30px 0 !important;
		}
	}
	.home {
		.home-details-container {
			margin: 0 auto;
		}
		.home-details {
			padding: 0;
			h1 {
				font-size: 38px;
				line-height: 48px;
				margin: 29px 0 13px;
			}
			p {
				margin: 10px 0 23px;
				font-size: 15px;
				line-height: 30px;
			}
		}
	}
	.main-img-mobile {
		border-radius: 50%;
		width: 270px;
		height: 270px;
		border: 4px solid #252525;
		margin: 0 auto 25px;
		display: block;
	}
}
@media (max-width: 576px) {
	.home {
		#menuToggle {
			right: 25px;
			top: 25px;
			padding: 17px 0 0 13px;
		}
		.home-details {
			h1 {
				font-size: 29px;
				line-height: 39px;
				margin: 18px 0 13px;
			}
			h6 {
				font-size: 19px;
			}
		}
	}
	.main-img-mobile {
		width: 230px;
		height: 230px;
	}
}
@media (max-width: 1200px) {
	.home {
		.home-details {
			h1 {
				padding-left: 0;
				&:before {
					display: none;
				}
			}
		}
	}
}
@media (max-width: 1200px) and (min-width: 992px) {
	.home {
		.home-details {
			h1 {
				font-size: 42px;
				line-height: 52px;
			}
		}
	}
}

@media (max-width: 1100px) and (min-width: 992px) {
	.home {
		.home-details {
			>div {
				max-width: 450px;
			}
		}
	}
}

@media (max-width: 767px) {
	.home {
		.home-details {
			p {
				font-size: 14px;
				line-height: 28px;
			}
		}
	}
}
@media (max-width: 576px) {

	body.home {
		font-size: 14px;
        .title-section {
            padding: 16px 25px 14px;
            background: #252525;
            border-bottom: 1px solid #333;
            margin-bottom: 36px;
            position: relative;
            h1 {
                font-size: 26px;
                text-align: center;
            }
        }
	}
    body.home {
        #blog {
            .container {
                padding: 0 0px !important;
            }
        }
    }

}
